.codesweetly-rigg-image-figure {
  margin: 0;
  position: relative;
}

.codesweetly-rigg-image-figure img {
  display: block;
  width: 100%;
  cursor: pointer;
}

.codesweetly-rigg-image-figure figcaption {
  opacity: 0;
  position: absolute;
  bottom: 0;
  z-index: 1000;
  width: 100%;
  background-color: rgba(0, 0, 0, 0.5);
  background: linear-gradient(180deg, rgba(25, 27, 38, 0), rgba(25, 27, 38, 1));
  padding: 16px;
  font-size: 0.85rem;
  text-align: center;
  color: #fff;
}

.codesweetly-rigg-image-figure:hover figcaption {
  opacity: 1;
}
